.Contact {
	position:absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	overflow: hidden;
	background-color: #66da89;
}

.ContactJimi {
	left: -200px;
}

.ContactWrapper {
	position: relative;
	margin: 0;
	top: 0;
	height: 100%;
	width: 1200px;
	min-width: 1200px;
	margin: 0 auto;
}

.ContactFields {
	text-align: right;
	position: absolute;
	height: 300px;
	top: calc(50% - 150px);
	width: auto;
	right: 100px;
	margin: 0;
}

.ContactFields a {
	margin: 1rem;
	font-size: 48px;
}

.ContactField {
	text-transform: uppercase;
	text-align: right;
	font-weight: normal;
	margin: 1rem;
	font-size: 18px;
}

.back {
	position: absolute;
	right: 100px;
	bottom: 0;
	font-weight: bold;
	margin: 1rem;
	margin-bottom: 5rem;
}

.MobileApp {
	background: url("/bgmobile.jpg") no-repeat center #eee;
	background-size: cover;
	width: 100%;
	height: 100%;
	position: absolute;
  font-family: 'jimi', Helvetica, Arial, Sans-Serif;
  text-align: center;
}

.MobileMain {
  width: 100%;
  color: yellow;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.MobileMain a {
  display: block;
  margin: 50px;
  cursor: pointer;
  font-size: 24px
}

.Contact.Mobile {
	background-color: #000;
	color: yellow;
}

.Contact.Mobile .ContactFields {
  right: 0;
  top: 50px;
}

.Contact.Mobile .ContactField {
  font-size: 14px;
}

.Contact.Mobile .back {
	position: absolute;
  width: 100%;
	bottom: 0;
  right: 0;
  margin: 0;
	margin-bottom: 5rem;
  text-align: center;
	font-weight: bold;
}

